@import url("https://fonts.googleapis.com/css2?family=Nabla&display=swap");
//font-family: 'Nabla', cursive;
@font-face {
  font-family: "Grobold";
  src: local("Open Sans"), url("./font/GROBOLD.ttf") format("truetype");
}
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");
//font-family: 'Montserrat', sans-serif;
@import url("https://fonts.googleapis.com/css2?family=Hind:wght@400;700&display=swap");
//font-family: 'Hind', sans-serif;
@import "./vars.scss";

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  overflow: hidden;
  font-family: $font-main;
}
html {
  height: 100%;
  scroll-behavior: smooth;
  overflow-y: scroll;
  min-height: 100vh;
  overflow-x: hidden;
  &::-webkit-scrollbar {
    width: 5px;
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $accentcolor;
  }
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px transparent;
    background-color: transparent;
    height: 50px;
  }
}
html:has(.wrapper#dark-theme) {
  background: $darkcolor;
  color: #fff;
}
html:has(.wrapper#light-theme) {
  background: #fff;
  color: #000;
}
.container {
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
  margin-top: 80px;
  margin-bottom: 50px;
}
.section-title {
  margin-bottom: 20px;
  font-size: 30px;
  font-family: $font-title;
}
.wrapper#dark-theme {
  .card-item {
    background: rgba($color: $darkseccolor, $alpha: 0.8);
    &.upc {
      background: #dbc4c4;
    }
    &.upc-over {
      background: #b42c2c;
      border: 1px solid #b42c2c;
      color: #fff;
    }
    &.anim {
      background: $accentcolor;
      width: auto;
      min-width: auto;
    }
  }

  .App.blur {
    background: initial;
  }
  form {
    select {
      background: url("./images/down-arrow.png") no-repeat right 0.8em center /
          1.4em,
        #ccc;
      color: #000;
    }
    input {
      background: #ccc;
      color: #000;
    }
    button {
      @media (hover: hover) and (pointer: fine) {
        :hover {
          background: #323437;
          color: #ffffff;
        }
      }
    }
  }
  header {
    background: $darkaccentcolor;
  }
  .addcard-popup .btns {
    button.active {
      background: #323437;
      color: #fff;
    }
  }
  .card-item.upc {
    background: #ccc;
    color: #000;
  }
  .reg-link {
    color: #fff;
  }
  .login-btn {
    background: #000;
  }
  .upcoming .nav a,
  .subscr .nav a {
    color: #000;
    background: #fff;
    @media (hover: hover) and (pointer: fine) {
      &:hover {
        background: $darkseccolor;
        color: #ffffff;
      }
    }
  }
  .settings {
    .settings-items {
      background: rgba($color: $darkcolor, $alpha: 0.2);
    }
    button {
      border: 1px solid #fff;
    }
  }
  .section-title {
    color: #fff;
  }
  .logout {
    color: #fff;
  }
  .settings div,
  .manage-emails {
    border-bottom: 1px solid rgba(#ccc, 0.2) !important;
  }
  .settingspage .settings,
  .emailslist-inner {
    background: rgba($color: $darkaccentcolor, $alpha: 0.25);
  }
}
