@import "../../mixin.scss";
@import "../../vars.scss";
.editpopup {
  @include popup-style;
  .form-inputs {
    @include border-radius;
    padding: 15px;
    margin: 20px 0;
    background: rgba($color: $darkcolor, $alpha: 0.2);
  }
  .nav {
    @include flexsb;
    h5 {
      font-size: 20px;
      text-align: center;
      width: 100%;
      font-family: $font-title;
    }
    button {
      cursor: pointer;
      outline: none;
      border: none;
      background: none;
      padding: 5px;
      @include flexc;
      @include transition;
      background: rgba($color: $darkaccentcolor, $alpha: 0.5);
      border-radius: 50px;
      -webkit-border-radius: 50px;
      -moz-border-radius: 50px;
      -ms-border-radius: 50px;
      -o-border-radius: 50px;
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: rgba($color: $darkaccentcolor, $alpha: 1);
        }
      }
    }
  }
  button[type="submit"] {
    @include button-submit;
    width: 100%;
    margin-top: 0;
  }
  form {
    display: flex;
    flex-direction: column;
    label {
      @include label-style;
      &.label1 {
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 10px;
      }
      &[for="price"] {
        flex-direction: column;
        justify-content: baseline;
        margin-bottom: 10px;
      }
      input[type="checkbox"] {
        @include input-checkbox-style;
      }
      input {
        @include input-style;
      }
    }
  }
  .btns {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    button {
      width: 49%;
      height: 30px;
      outline: none;
      border: 1px solid #fff;
      background: #505050;
      color: #fff;
      @include transition;
      &.active {
        background: #000;
      }
    }
  }
}
