@import "../../mixin.scss";
@import "../../vars.scss";

.auth-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  h1 {
    text-align: center;
    font-weight: 500;
    margin-top: 50px;
    margin-bottom: 30px;
  }
  form {
    max-width: 550px;
    width: 100%;
    div {
      display: flex;
      flex-direction: column;
      width: 100%;

      label {
        @include label-style;
      }
      input {
        @include input-style;
      }
    }
    .login-btn {
      @include button-submit;
      margin-top: 50px;
    }
  }
}
.resetpass {
  button {
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
    margin-top: 20px;
    text-align: left;
    font-weight: 500;
  }
}
.or-block {
  margin: 50px 0;
  text-align: center;
  color: #adadadea;
  position: relative;
  padding: 0 10px;
  &::after {
    position: absolute;
    content: "";
    width: 100px;
    height: 1px;
    background: #b3b3b3ea;
    top: 50%;
    left: 60px;
  }
  &::before {
    position: absolute;
    content: "";
    width: 100px;
    height: 1px;
    background: #b3b3b3ea;
    top: 50%;
    right: 60px;
  }
}
