@import "../../mixin.scss";
@import "../../vars.scss";

.settingspage {
  margin: 0 auto;
  margin-top: 80px;
  max-width: 600px;

  .settings {
    display: flex;
    flex-direction: column;
    @include float-sec;
    .settings-items {
      @include border-radius;
      @include box-shadow ;
      padding: 15px;
      margin: 20px 0;
      background: rgba($color: #fff, $alpha: 0.6);
      div {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 0 10px 0;
        @include float-sec-border;
        padding-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
        span {
          font-size: 17px;
          @media (max-width: 700px) {
            font-size: 15px;
          }
        }
        button {
          @include button-submit;
          width: 75px;
          margin: 0;
          height: 25px;
          font-size: 14px;
          @media (max-width: 700px) {
            font-size: 12px;
          }
        }
      }

      .editnu {
        display: flex;
        align-items: center;
        position: relative;
        button {
          font-size: 16px;
          outline: none;
          position: absolute;
          right: 1px;
          width: 40px;
          height: 28px;
          background: $accentcolor;
          border-radius: 0 15px 15px 0;
          -webkit-border-radius: 0 15px 15px 0;
          -moz-border-radius: 0 15px 15px 0;
          -ms-border-radius: 0 15px 15px 0;
          -o-border-radius: 0 15px 15px 0;
        }
        input {
          height: 30px;
          outline: none;
          border: 1px solid #000;
          @include border-radius;
          padding: 5px 30px 5px 5px;
          &:focus {
            background: rgba($color: #fff, $alpha: 1);
            color: #000;
          }
        }
      }
    }
    .managemails {
      all: unset;
      border: none !important;
      display: flex;
      justify-content: space-between;
      align-items: center;
      outline: none;
      font-size: 15px;
      font-size: 17px;
      margin-bottom: 0px;
      @include transition;
      cursor: pointer;
      img {
        width: 25px;
        height: 25px;
      }
    }
    .logout {
      cursor: pointer;
      width: 100%;
      height: 35px;
      margin: 0 auto;
      background: none;
      font-size: 16px;
      outline: none;
      font-weight: 500;
      @include border-radius;

      @include transition;
      border: 1px solid #000;
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: #000000;
          color: #ffffff;
          border: 1px solid #ffffff;
        }
      }
      @media (max-width: 660px) {
        width: 200px;
      }
    }
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 28px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $darkseccolor;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: $accentcolor;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px $accentcolor;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(18px);
    -ms-transform: translateX(18px);
    transform: translateX(18px);
    -moz-transform: translateX(18px);
    -o-transform: translateX(18px);
  }

  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }
}
