@import "./vars";
@mixin border-radius {
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
@mixin box-shadow {
  box-shadow: 0px 0px 5px 1px rgba($darktextcolor, 0.6);
}
@mixin transition {
  transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
  -moz-transition: all 0.3s ease-in;
  -ms-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
}
@mixin font($size) {
  font-size: $size;
  line-height: 1.2;
  font-weight: 400;
  letter-spacing: -0.022em;
}
@mixin flexc {
  display: flex;
  justify-content: center;
  align-items: center;
}
@mixin flexsb {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@mixin popup-style {
  z-index: 10;
  background: $darkseccolor;
  color: #fff;
  position: absolute;
  top: 20%;
  left: 50%;
  padding: 25px;
  width: 500px;
  height: auto;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  @include border-radius;
  @include box-shadow;
  @media (max-width: 700px) {
    width: 90%;
    max-width: 500px;
    min-width: 290px;
    padding: 10px;
  }
}
@mixin label-style {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  margin-bottom: 15px;
  font-size: 18px;
  @media (max-width: 700px) {
    font-size: 15px;
  }
}
@mixin input-style {
  @include border-radius;
  outline: none;
  padding: 8px;
  font-size: 16px;
  width: 100%;
  margin-top: 5px;
  border: 2px solid #ccc;
  background: #e6e6e6;
  @media (max-width: 700px) {
    font-size: 15px;
  }
  &:focus {
    border: 2px solid #fff;
  }
}
@mixin input-checkbox-style {
  width: 25px;
  height: 25px;
}
@mixin button-submit {
  @include border-radius;
  @include flexc;
  outline: none;
  cursor: pointer;
  width: 150px;
  font-weight: 700;
  text-transform: capitalize;
  background: $darkcolor;
  height: 40px;
  color: #fff;
  margin: 0 auto;
  font-size: 16px;
  @include transition;
  border: 1px solid $darkcolor;
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      background: $darkaccentcolor;
      color: #000;
      border: 1px solid $darkaccentcolor;
    }
  }
}
@mixin scrollbar-style {
  &::-webkit-scrollbar {
    width: 5px;
    height: 7px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    @include border-radius;
  }

  &::-webkit-scrollbar-thumb {
    background: #86eb03;
    @include border-radius;
  }
}
@mixin float-sec {
  padding: 30px 20px;
  @include border-radius;
  background: rgba($color: $darkaccentcolor, $alpha: 0.5);
}
@mixin float-sec-border {
  border-bottom: 1px solid rgba($darkaccentcolor, 0.3) !important;
}
