@import "../../mixin.scss";
@import "../../vars.scss";

.linkblock{
  max-width: 300px;
  width: 100%;
  margin: 0 auto;
  margin-top: 50px;
  .reg {
    display: flex;
    justify-content: space-between;
    align-items: center;
    a{
      @include button-submit;
      width: 45%;
      text-decoration: none;
      margin: 0;
      height: 40px;
      font-size: 17px;
    }
  }
  .btn-text{
    font-size: 17px;
  }
}