@import "../../mixin.scss";
@import "../../vars.scss";
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  z-index: 12;
  .logo {
    img {
      width: 35px;
      height: 35px;
    }
  }
  nav {
    ul {
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: 10px 10px 0 10px;
      @media (max-width: 700px) {
        justify-content: space-between;
      }
      li {
        list-style: none;
        a {
          padding: 5px 10px;
          padding-bottom: 10px;
          text-decoration: none;
          color: #000;
          font-weight: 500;
          text-transform: uppercase;
          display: flex;
          justify-content: center;
          align-items: center;
          &.active:not(.logo-li a) {
            border-bottom: 3px solid $accentcolor;
          }
          @media (max-width: 700px) {
            img,svg {
              width: 30px;
              height: 30px;
            }
          }
        }
      }
      .links {
        display: flex;
        align-items: center;
      }
    }
  }
}
