@import "../../mixin.scss";
@import "../../vars.scss";

.intro {
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
  @include flexc;
  flex-direction: column;
  height: 80vh;
  h1{
    font-size: 40px;
    font-family: $font-accent2;
    font-weight: 700;
    text-align: center;
    margin-bottom: 50px;
    line-height: 1.5;
    @media (max-width:550px) {
      font-size: 25px;
    }
  }
  .linkblock{
    margin-top: 10px;
    max-width: 500px;
  }
  .read-more {
    display: block;
    color: #fff;
    background: $accentcolor;
    height: 40px;
    @include flexc;
    margin: 0 auto;
    text-decoration: none;
    font-size: 18px;
    width: 100%;
    font-weight: 700;
    border: 3px solid $accentcolor;
    @include transition;
    margin-bottom: 20px;
    @media (hover: hover) and (pointer: fine) {
    
      &:hover {
        border: 3px solid #000;
        color: #000;
      }
    }
  }
}
