@import "../../mixin.scss";
@import "../../vars.scss";
.nocards {
  text-align: center;
  margin-top: 40px;
  h4 {
    font-size: 25px;
  }
  p {
    font-size: 20px;
    margin: 20px 0 10px 0;
  }
  .addcard {
    margin: 0 auto;
    position: unset;
    width: 100px;
    height: 40px;
    @include border-radius;
  }
}
.addcard {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  position: fixed;
  bottom: 15px;
  right: 15px;
  outline: none;
  cursor: pointer;
  z-index: 100;
  border: 4px solid transparent;
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      border: 4px solid $accentcolor;
    }
  }
}
.cards {
  margin-top: 30px;
  .nav {
    h2 {
      margin-bottom: 0;
    }
  }
  h2 {
    margin-bottom: 20px;
    @media (max-width: 660px) {
      text-align: center;
    }
  }

  .subscr-items,
  .nosub-items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 25px;
    column-gap: 25px;
    margin-bottom: 20px;
    @media (max-width: 1000px) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 660px) {
      grid-template-columns: 1fr;
      justify-items: center;
    }
  }
  .nosub-items {
    .card-item {
      flex-direction: row;
      justify-content: space-between;
      .info {
        display: flex;
        align-items: center;
        h4 {
          font-size: 18px;
        }
      }
      .btn-block {
        margin: 0;
        button {
          margin-left: 5px;
        }
      }
    }
  }
}
