@import "../../mixin.scss";
@import "../../vars.scss";
.addemail-popup,
.addcard-popup {
  @include popup-style;
  .form-inputs {
    @include border-radius;
    @include box-shadow;
    padding: 15px;
    margin: 20px 0;
    background: rgba($color: $darkcolor, $alpha: 0.2);
  }
  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h5 {
      font-size: 20px;
      text-align: center;
      width: 100%;
      font-family: $font-title;
      @media (max-width: 700px) {
        font-size: 18px;
      }
    }
    button {
      outline: none;
      border: transparent;
      cursor: pointer;
      padding: 5px;
      font-size: 16px;
      @include flexc;
      @include transition;
      background: rgba($color: $darkaccentcolor, $alpha: 0.5);
      border-radius: 50px;
      -webkit-border-radius: 50px;
      -moz-border-radius: 50px;
      -ms-border-radius: 50px;
      -o-border-radius: 50px;
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: rgba($color: $darkaccentcolor, $alpha: 1);
        }
      }
    }
  }
  button[type="submit"] {
    @include button-submit;
    width: 100%;
    @include border-radius;
  }
  label {
    &:last-child {
      margin-bottom: 0;
    }
    @include label-style;
    &[for="main"],
    &[for="subscr"] {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      input {
        width: 25px;
        height: 25px;
      }
    }
    input {
      @include input-style;
    }
  }
  select {
    appearance: none;
    padding: 12px 8px;
    font-size: 16px;
    outline: none;
    width: 100%;
    border: 1px solid #ccc;
    @include border-radius;
    background: url("../../images/down-arrow.png") no-repeat right 0.8em center /
        1.4em,
      #e6e6e6;
    cursor: pointer;
    &::-ms-expand {
      display: none;
    }
    &:focus {
      outline: none;
    }

    &[name="emailtype"] {
      margin-top: 5px;
    }
  }
  .btns {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    button {
      width: 45%;
      height: 30px;
      outline: none;
      border: 1px solid #fff;
      background: #505050;
      color: #fff;
      @include transition;
      &.active {
        background: #000;
      }
    }
  }
  p.warn {
    text-align: center;
    font-size: 16px;
    margin: 10px;
    color: red;
  }

  .addemail-popup {
    top: 33%;
  }
}
