@import "../../mixin.scss";
@import "../../vars.scss";

.card-item {
  position: relative;
  padding: 15px;
  height: auto;
  background: #e2e2e2;
  display: flex;
  flex-direction: column;
  @include box-shadow;
  @include transition;
  @include border-radius;
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      box-shadow: none;
    }
  }
  @media (max-width: 660px) {
    min-width: 280px;
    width: 100%;
    max-width: 500px;
  }
  .price-block {
    margin-top: 5px;
    margin-left: auto;
    text-align: end;
  }
  .daysleft {
    padding-top: 10px;
  }
  .btn-block {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    button {
      outline: none;
      padding: 5px;
      border: none;
      background: none;
      cursor: pointer;
      img {
        width: 20px;
        height: 20px;
      }
    }
  }
  .info-data {
    .date {
      @media (max-width: 700px) {
        font-size: 13px;
      }
    }
  }
  .data {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 20px;
    .data-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .main-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  .info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  h4 {
    font-size: 20px;
    text-wrap: nowrap;
    @media (max-width: 700px) {
      font-size: 16px;
    }
  }
  span.period {
    font-family: $font-accent;
    top: 5px;
    right: 15px;
    display: block;
    font-size: 23px;
    font-weight: 700;
    text-transform: uppercase;
    margin-left: 10px;
  }
  &.upc {
    background: #dbc4c4 !important;
  }
  &.upc-over {
    background: #b42c2c;
    border: 1px solid #b42c2c;
    color: #fff;
  }

  .link-button {
    display: flex;
    justify-content: center;
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
  }
}
$radius: 55px;
$circumf: 3.1416 * $radius * 2;
$check-len: 75px;
.cnfrm {
  display: flex;
  justify-content: center;
  align-items: center;
  svg#check-anim {
    width: 65px;
    height: 65px;
  }
  @keyframes outline {
    from {
      stroke-dasharray: 0, $circumf;
    }
    to {
      stroke-dasharray: $circumf, $circumf;
    }
  }

  #outline {
    animation: 0.38s ease-in outline;
    transform: rotate(0deg);
    transform-origin: center;
  }

  @keyframes circle {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(0);
    }
  }

  #white-circle {
    animation: 0.35s ease-in 0.35s forwards circle;
    transform: none;
    transform-origin: center;
  }

  @keyframes check {
    from {
      stroke-dasharray: 0, $check-len;
    }
    to {
      stroke-dasharray: $check-len, $check-len;
    }
  }

  #check {
    animation: 0.34s cubic-bezier(0.65, 0, 1, 1) 0.8s forwards check;
    stroke-dasharray: 0, $check-len;
  }

  @keyframes check-group {
    from {
      transform: scale(1);
    }

    50% {
      transform: scale(1.09);
    }

    to {
      transform: scale(1);
    }
  }

  #check-group {
    animation: 0.32s ease-in-out 1.03s check-group;
    transform-origin: center;
  }
}
