@import "../../mixin.scss";
@import "../../vars.scss";

.loader {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: $darkcolor;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    margin-bottom: 50px;
    @media (max-width: 700px) {
      width: 55px;
      height: 55px;
    }
  }
}
.loader-body {
  width: 200px;
  height: 5px;
  border-radius: 2px;
  background-color: rgba(0, 0, 0, 0.2);
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  @media (max-width: 700px) {
    bottom: 10%;
  }
  &::before {
    content: "";
    position: absolute;
    background-color: $accentcolor;
    width: 0%;
    height: 100%;
    border-radius: 2px;
    animation: load 2s ease-in-out infinite;
    box-shadow: $accentcolor 0px 2px 29px 0px;
    -webkit-animation: load 2s ease-in-out infinite;
  }
}

.loader-inner {
  display: flex;
  justify-content: center;
}

@keyframes load {
  50% {
    width: 100%;
  }

  100% {
    right: 0;
    left: unset;
  }
}
