@import "../../mixin.scss";
@import "../../vars.scss";
.subscriptions {
  @media (max-width: 700px) {
    margin-top: 70px;
  }
  .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;

    @media (max-width: 660px) {
      justify-content: space-between;
    }
    .search-block {
      @include flexc;
      .inp-block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        button {
          position: absolute;
          right: 8px;
          top: 0;
          bottom: 0;
          @include flexc;
          svg {
            margin-right: 0;
            width: 25px;
            height: 25px;
          }
        }
      }
      input {
        @include input-style;
        margin-top: 0;
        padding-right: 40px;
      }
      button {
        background: none;
        border: none;
        outline: none;
        cursor: pointer;
      }
      img {
        margin-right: 15px;
        width: 30px;
        height: 30px;
      }
    }
    .total {
      display: flex;
      align-items: center;

      span {
        margin-left: 30px;
        font-size: 20px;
        font-weight: 700;
      }
    }
  }
}
.subs-items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 20px;
  column-gap: 20px;
  @media (max-width: 1000px) {
    grid-template-columns: 1fr 1fr;
  }
  @media (max-width: 660px) {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
