@import "../../mixin.scss";
@import "../../vars.scss";

.google-btn {
  cursor: pointer;
  margin-top: 20px;
  max-width: 550px;
  width: 100%;
  height: 42px;
  background-color: #000000;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: space-between;
  flex-direction: row !important;
  align-items: center;
  .google-icon-wrapper {
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
  }
  .google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: 11px;
    width: 18px;
    height: 18px;
  }

  .btn-text {
    margin: auto;
    color: #fff;
    font-weight: 300;
    letter-spacing: 0.2px;
    padding: 0 20px;
  }
  @media (hover: hover) and (pointer: fine) {
 
    &:hover {
      box-shadow: 0 0 6px #000000;
    }
  }
  &:active {
    background: #000000;
  }
}
