@import "../../mixin.scss";
@import "../../vars.scss";
.navbar {
  display: flex;
  align-items: center;
  position: relative;
  .navbar-items {
    padding: 10px 0;
    display: flex;
    align-items: center;
    margin-right: 70px;
    margin-left: 45px;
    overflow-x: auto;
    @include border-radius;
    @include scrollbar-style;
    @media (max-width: 1230px) {
  margin-right: 45px;
    }
    @media (max-width: 700px) {
      margin-left: 40px;
      margin-right: 35px;
    }
  }
  button.addpopup {
    top: 10px;
    position: absolute;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #000;
    min-width: 35px;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    outline: none;
    cursor: pointer;
    transform: translate(-50%);
    -webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    img {
      width: 20px;
      height: 20px;
    }
    border: 2px solid transparent;
    @media (hover: hover) and (pointer: fine) {
      &:hover {
        border: 2px solid $accentcolor;
      }
    }
    @media (max-width: 1230px) {
      right: -20px;
    }
    @media (max-width: 700px) {
right: -25px;
    }
  }
}
button.home {
  top: 10px;
  position: absolute;
  left: 10px;
  min-width: 40px;
  width: 40px;
  height: 40px;
  outline: none;
  border: transparent;
  cursor: pointer;
  transform: translate(-50%);
  -webkit-transform: translate(-50%);
  -moz-transform: translate(-50%);
  -ms-transform: translate(-50%);
  -o-transform: translate(-50%);
  background: #c9c9c9;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  @include flexc;
  img {
    width: 20px;
    height: 20px;
  }
  &.active {
    background: $accentcolor;
  }
}
.navbar-item {
  cursor: pointer;
  width: fit-content;
  background: $darkaccentcolor;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  @include border-radius;
  @include transition;
  margin-left: 10px;
  color: #fff;
  img {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
  &:first-child {
    margin-left: 0;
  }
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      background: $darkseccolor;
    }
  }
  &.active {
    background: $darkseccolor;
  }
  span {
    text-wrap: nowrap;
  }
}
