@import "../../mixin.scss";
@import "../../vars.scss";
.home-main {
  margin-top: 40px;
  .subscr-items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 25px;
    column-gap: 25px;
    @media (max-width: 1000px) {
      grid-template-columns: 1fr 1fr;
      row-gap: 20px;
      column-gap: 20px;
    }
    @media (max-width: 660px) {
      grid-template-columns: 1fr;
      justify-items: center;
      row-gap: 15px;
      column-gap: 15px;
    }
  }
}
.upcoming,
.subscr {
  .nav {
    @include flexsb;
    margin-bottom: 30px;
    @media (max-width: 700px) {
      margin-bottom: 20px;
    }
    a {
      text-decoration: none;
      color: #fff;
      padding: 5px 10px;
      background: #000;
      @include transition;
      @media (max-width: 700px) {
        font-size: 14px;
      }
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: $darkseccolor;
        }
      }
    }
    h2 {
      font-family: $font-title;
      @media (max-width: 700px) {
        font-size: 20px;
      }
    }
  }
}
.upcoming {
  margin-bottom: 40px;
  .upcoming-items {
    display: flex;
    align-items: center;
    overflow-x: auto;
    padding-bottom: 15px;
    @include scrollbar-style;
  }
  .card-item {
    min-width: 200px;
    width: 200px;
    padding: 20px;
    margin-left: 10px;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-shadow: none;
    &:has(.confirm) {
      width: 300px;
      min-width: 300px;
      @media (max-width: 660px) {
        min-width: 280px;
      }
      .data {
        margin-right: 20px;
      }
    }
    .confirm {
      border: transparent;
      background: transparent;
      cursor: pointer;
      @include transition;
      z-index: 2;
      width: 50px;
      img {
        width: 50px;
        height: 50px;
        border: 5px solid #fff;
        background: #fff;
        border-radius: 20px;
        -webkit-border-radius: 20px;
        -moz-border-radius: 20px;
        -ms-border-radius: 20px;
        -o-border-radius: 20px;
        @media (hover: hover) and (pointer: fine) {
          &:hover {
            border: 5px solid #ccc;
            background: #ccc;
          }
        }
      }
    }
    &:first-child {
      margin-left: 0;
    }
    .info {
      display: flex;
      justify-content: space-between;
      width: 100%;
      .price-block {
        margin: 0;
      }
    }
    .info-data {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
    }
    &.anim{
      background: $accentcolor;
      width: auto;
      min-width: auto;
    }
  }
}
